.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 10%;
}
.about_me {
  width: 80%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background-image: linear-gradient(45deg, transparent, var(--colorPrimary));
  display: grid;
  place-items: center;
  transition: var(--transition);
  margin: auto;
}
.about_me:hover {
  width: 82%;
}
.about_me_image {
  border-radius: 2rem;
  overflow: hidden;
  /* transform: rotate(10deg); */
  transition: 1s;
}
.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.about_card {
  background: var(--colorBgVariant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}
.about_card:hover {
  background: transparent;
  border-color: var(--colorPrimaryVariant);
  cursor: default;
}
.about_card h5 {
  font-size: 0.9rem;
}
.about_card small {
  font-size: 0.7rem;
  color: var(--colorLight);
}
.about_content p {
  color: var(--colorLight);
  margin: 2rem 0 2.6rem;
}
/* ========================================media queries for ( MEDIUM DEVICES ) ================================ */
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about_content p {
    margin: 1rem 0 1.5rem;
  }
}
/* ========================================media queries for ( SMALL DEVICES ) ================================ */
@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about_cards {
    grid-template-columns: 1fr 1fr;
  }
  .about_content {
    text-align: center;
  }
  .about_content p {
    margin: 1.5rem 0;
  }
}
